.home {
   
    .grid-container {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-flow: wrap;
        gap: 1px;
        width: 100vw;

        .image-card{
            display: flex;

            img{
                width: 33vw;
                margin: 0 auto;
            }
            .cart-text{
                display: none;
                text-transform: uppercase;
                color: rgb(212, 255, 255);
                font-size: 28px;
                // text-align: center;
                // margin-left: 20px;
                transition: all ease-in-out 0.8s;

            }
           
        }
        .image-card:hover {
            position: relative;
            transition: all ease-in-out 0.4s;

            .cart-text{
                display: block;
                position: absolute;
                font-family: 'Righteous', cursive;
                background-color: rgba(0, 0, 0, 0.4);
                // opacity: 0.2;
                width: 100%;
                height: 100%;


            }
        }
    }


}

@media (max-width: 500px) {
    .home {
        .grid-container {
            .image-card{
                img {
                    width: 45vw;
                }
                .cart-text{
                    font-size: 15px;
                }
            }
        }

    }
}

@media (max-width: 420px) {
    .home {
        .grid-container {
            .image-card{
                img {
                    width: 90vw;
                }
                .cart-text{
                    font-size: 15px;
                }
            }
        }

    }
}
