.project-pages {
    // width: 100%;
    // margin: 0 auto;

    // background-color: rgb(195, 195, 195);
    background-image: url('../../assets/backgroundSite.png');
    background-position: fixed;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    
    h2 {
        font-size: 84px;
        font-weight: bolder;
    }
    hr {
        width: 90%;
        margin: 0 auto;

    }
    h3 {
        font-size: 25px;
        font-weight: bolder;
        background-color: white;
        border: 1px solid black;
    }
    h3, h4 {
        font-family: 'Righteous', cursive;

    }
   
    .art-description{
        text-align: justify;
        width: 80%;
        margin: auto;
        padding: 20px;
        font-size: 20px;
        font-family: 'Courier New', Courier, monospace;
    }
    .project-navigation {
        display: flex;
        justify-content: space-between;
        width: 80%;
        margin: 10px auto;
        a {
            text-decoration: none;
        }
        .nav-btn {
            border: 2px solid black;
            border-radius: 20px;
            width: 120px;
            font-family: 'Righteous', cursive;
            font-weight: bold;
            background-color: rgb(255, 255, 255);
            display: flex;
            justify-content: space-between;
        }
    }
    .making-off {
        width: 90%;
        margin: 0 auto;

        display: flex;
        justify-content: space-evenly;
        text-align: left;
        
        li {
            text-transform: none;
            font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
        }
    }
}

@media (max-width: 1080px) {
    .project-pages {
        h2 {
        font-size: 54px;

        }

    }
}

@media (max-width: 790px) {
    .project-pages {
        h2 {
        font-size: 34px;

        }
        h3 {
            font-size: 20px;
            font-weight: bolder;
        }
    }
}
@media (max-width: 640px) {
    .project-pages {
        h2 {
        font-size: 24px;

        }
        h3 {
            font-size: 17px;
            font-weight: bolder;
        }
    }
}
@media (max-width: 530px) {
    .project-pages {
        h2 {
        font-size: 22px;

        }
        h3 {
            font-size: 15px;
            font-weight: bolder;
        }
    }
}
@media (max-width: 460px) {
    .project-pages {
        h2 {
        font-size: 22px;

        }
        h3 {
            font-size: 15px;
            font-weight: bolder;

        }
    }
}
